		width: 16.6%;
	}
	.column.flow-opposite {
		float: right;
	}
	.column.centered {
		margin-left: 15%;
		width: 70%;
	}
}
@media (min-width: 1090px) {
	.container {
		margin: 0 auto;
		width: 960px;
	}
}
/*--------------------------------------------------------------3.0 Typography--------------------------------------------------------------*/
body {
	letter-spacing: 1px;
}
body,
button,
input,
select,
textarea {
	color: #666666;
	font-family: inherit;
	font-size: 14px;
	line-height: 1.8;
	word-wrap: break-word;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	-ms-hyphens: auto;
	hyphens: auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 700;
	margin-top: 20px;
	margin-bottom: 10px;
}
h1 {
	font-size: 36px;
}
h2 {
	font-size: 30px;
}
h3 {
	font-size: 24px;
}
h4 {
	font-size: 18px;
}
h5 {
	font-size: 14px;
}
h6 {
	font-size: 12px;
}
p {
	margin-bottom: 1.5em;
}
p:last-child {
	margin-bottom: 0;
}
b,
strong {
	font-weight: bold;
}
dfn,
cite,
em,
i {
	font-style: italic;
}
blockquote {
	border-left: 5px solid #ffd700;
	margin-bottom: 20px;
	padding: 10px 20px;
}
address {
	font-style: italic;
	margin-bottom: 1.5em;
}
pre {
	background: #eee;
	/* font-family: "Courier 10 Pitch", Courier, monospace; */
	font-size: 15px;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}
code,
kbd,
tt,
var {
	background: #eee;
	color: #222;
	font: 15px Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	padding: 0;
	white-space: normal;
}
abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}
mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}
sup,
sub {
	font-size: 75%;
	height: 0;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sup {
	bottom: 1ex;
}
sub {
	top: 0.5ex;
}
small {
	font-size: 75%;
}
big {
	font-size: 125%;
}
/*--------------------------------------------------------------4.0 Elements--------------------------------------------------------------*/
hr {
	background-color: #e5e5e5;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
	margin-top: 1.5em;
}
img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}
ul,
ol {
	margin: 0 0 1.5em 3em;
}
ul {
	list-style: disc;
}
ol {
	list-style: decimal;
}
li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}
dt {
	font-weight: bold;
	color: #0a0700;
}
dd {
	margin-bottom: 1.5em;
}
figure {
	margin: 0;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	line-height: 2;
	margin-bottom: 40px;
	width: 100%;
}
table th {
	font-weight: bold;
	padding: 10px 10px 10px 0;
	text-align: left;
}
table td {
	padding: 10px 10px 10px 0;
}
table tr {
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
/*--------------------------------------------------------------5.0 Forms--------------------------------------------------------------*/
button,
input,
select,
textarea {
	font-size: 100%;
	/* Corrects font size not being inherited in all browsers */
	margin: 0;
	/* Addresses margins set differently in IE6/7,F3/4,S5,Chrome */
	vertical-align: baseline;
	/* Improves appearance and consistency in all browsers */
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid #ccc;
	border-radius: 0px;
	background: #ccc;
	color: #fff;
	cursor: pointer;
	/* Improves usability and consistency of cursor style between image-type 'input' and others */
	-webkit-appearance: button;
	/* Corrects inability to style clickable 'input' types in iOS */
	padding: 3px 15px;
}
button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	opacity: 0.6;
}
input[type="checkbox"],
input[type="radio"] {
	padding: 0;
	/* Addresses excess padding in IE8/9 */
}
input[type="search"] {
	-webkit-appearance: textfield;
	/* Addresses appearance set to searchfield in S5,Chrome */
	-webkit-box-sizing: content-box;
	/* Addresses box sizing set to border-box in S5,Chrome (include -moz to future-proof) */
	-moz-box-sizing: content-box;
	box-sizing: content-box;
}
input[type="search"]::-webkit-search-decoration {
	/* Corrects inner padding displayed oddly in S5,Chrome on OSX */
	-webkit-appearance: none;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
	/* Corrects inner padding and border displayed oddly in FF3/4 www.sitepen.com/blog/2008/05/14/the-devils-in-the-details-fixing-dojos-toolbar-buttons/ */
	border: 0;
	padding: 0;
}
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	border: 1px solid #ccc;
	border-radius: 0px;
	color: #666;
}
input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
textarea:focus {
	color: #111;
}
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="tel"] {
	padding: 6px;
	border: 1px solid #ccc;
}
textarea {
	overflow: auto;
	/* Removes default vertical scrollbar in IE6/7/8/9 */
	padding: 5px;
	vertical-align: top;
	/* Improves readability and alignment in all browsers */
	width: 98%;
}
/*--------------------------------------------------------------5.1 Contact Form 7--------------------------------------------------------------*/
.wpcf7-form-control-wrap {
	display: inline;
}
.wpcf7 input[type="text"],
.wpcf7 input[type="email"],
.wpcf7 input[type="url"],
.wpcf7 input[type="password"],
.wpcf7 textarea {
	background-color: #e6e6e6;
	border: 1px solid #e6e6e6;
	border-radius: 0;
	outline-color: transparent;
	outline-style: none;
	padding: 10px;
	width: 100%;
}
.wpcf7 input[type="text"]:focus,
.wpcf7 input[type="email"]:focus,
.wpcf7 input[type="url"]:focus,
.wpcf7 input[type="password"]:focus,
.wpcf7 textarea:focus {
	background-color: #fff;
	color: #111;
}
.wpcf7 input[type="submit"] {
	border: 1px solid #4d4d4d;
	border-color: #4d4d4d;
	border-radius: 0;
	background: #4d4d4d;
	box-shadow: none;
	color: #fff;
	cursor: pointer;
	/* Improves usability and consistency of cursor style between image-type 'input' and others */
	-webkit-appearance: button;
	/* Corrects inability to style clickable 'input' types in iOS */
	font-size: 14px;
	font-size: 1.4rem;
	line-height: 1;
	outline-color: transparent;
	outline-style: none;
	padding: 1em 1em 0.9em;
	text-shadow: 0;
	width: 100%;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
.wpcf7 input[type="submit"]:hover {
	opacity: 0.6;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
/*--------------------------------------------------------------6.0 Header--------------------------------------------------------------*/
html {
	border: 12px solid #111;
}
.site-header {
	padding-top: 2em;
	padding-bottom: 1em;
}
.header-image {
	margin-top: 1em;
	text-align: center;
	margin-bottom: 2em;
}
/*--------------------------------------------------------------6.1 Links--------------------------------------------------------------*/
a {
	color: inherit;
	text-decoration: none;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
a:visited {
	color: #666666;
}
a:hover {
	color: #ccc;
	text-decoration: none;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
a:active,
a:focus {
	text-decoration: none;
}
/*--------------------------------------------------------------6.2 Logo and Title--------------------------------------------------------------*/
.site-branding {
	margin-bottom: 1em;
	text-align: center;
}
.site-title {
	/* font-family: "Quattrocento Sans", sans-serif; */
	font-size: 60px;
	font-weight: 300;
	letter-spacing: 2px;
	line-height: 1;
	margin-top: 25px;
	margin-bottom: 20px;
	text-transform: uppercase;
}
.site-title a {
	text-decoration: none;
}
.site-description {
	font-size: 14px;
	font-style: italic;
	font-weight: 300;
	margin-top: -15px;
	margin-bottom: 35px;
	padding-top: 0;
}
/*--------------------------------------------------------------6.3 Menus--------------------------------------------------------------*/
.menu {
	display: table;
	margin: 0 auto;
}
.main-navigation {
	background-color: #fff;
	font-size: 14px;
	font-weight: 700;
	letter-spacing: 1px;
	padding: 15px 0;
	border-top: 1px solid;
	border-bottom: 1px solid;
	text-transform: uppercase;
}
.main-navigation a {
	text-decoration: none;
}
.main-navigation ul {
	list-style: none;
	margin: 0 auto;
	padding: 3px 0;
}
.main-navigation li {
	display: inline;
	float: left;
	margin: 0 1em;
	position: relative;
}
.main-navigation ul ul {
	display: none;
	text-transform: none;
	box-shadow: none;
	opacity: 0.9;
	float: left;
	left: -1em;
	padding: 0.5em 1em;
	position: absolute;
	top: 1.5em;
	z-index: 99999;
	padding-top: 35px;
	transition: all 0.3s;
	font-weight: 400;
	background-color: #fff;
}
.main-navigation ul ul li {
	margin: 0;
	width: 150px;
}
.main-navigation ul li:hover > ul {
	display: block;
}
/* Small menu */
.menu-toggle {
	display: none;
}
/* Paging Navigation */
.pagination {
	clear: both;
	padding: 15px 15px 30px 15px;
	overflow: hidden;
	text-align: center;
}
.pagination a {
	padding: 5px 10px;
	margin-right: 2px;
	background: #ccc;
	color: #fff;
	text-decoration: blink;
}
.pagination a:hover,
.pagination .current {
	padding: 5px 10px;
	margin-right: 2px;
	background: #ffd700;
	color: #fff;
}
.site-main .comment-navigation,
.site-main .paging-navigation {
	margin: 15px 0 30px;
	overflow: hidden;
}
.site-main .post-navigation {
	margin: 15px 0 0px;
	overflow: hidden;
}
.paging-navigation {
	padding: 0 15px;
}
.comment-navigation .nav-previous,
.paging-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
	padding-bottom: 2.5em;
}
.comment-navigation .nav-next,
.paging-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}
/*--------------------------------------------------------------7.0 Accessibility--------------------------------------------------------------*/
/* Text meant only for screen readers */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
}
.screen-reader-text:hover,
.screen-reader-text:active,
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar */
}
/*--------------------------------------------------------------8.0 Alignments--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin: 0.5em 1.5em 1em 0;
}
.alignright {
	display: inline;
	float: right;
	margin: 0.5em 0 1em 1.5em;
}
.aligncenter {
	clear: both;
	display: block;
	margin: 0 auto;
}
/*--------------------------------------------------------------9.0 Clearings--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
}
.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}
.clear {
	*zoom: 1;
}
/*--------------------------------------------------------------10.0 Widgets--------------------------------------------------------------*/
.widget-area {
}
.widget {
	border: 0;
	margin: 0 0 2em 0;
	padding: 0;
	word-wrap: break-word;
}
.widget a {
	text-decoration: none;
}
.widget ul {
	list-style: none;
	margin-left: 0;
}
.widget-title {
	font-size: 14px;
	font-weight: 700;
	letter-spacing: 4px;
	list-style: none;
	margin-top: 0;
	margin-left: 0;
	margin-bottom: 1em;
	text-transform: uppercase;
	text-align: left;
}
.widget-title a {
	text-decoration: none;
}
/* Make sure select elements fit in widgets */
.widget select {
	max-width: 100%;
}
/* Search widget */
.widget_search .search-submit {
	display: none;
}
.widget_search input[type="search"] {
	width: 70%;
}
.widget_recent_comments ul li:before,
.widget_categories ul li:before,
.widget_archive ul li:before,
.widget_links ul li:before,
.widget_meta ul li:before,
.widget_pages ul li:before,
.widget_recent_entries ul li:before {
	content: "\f105";
	/* font-family: FontAwesome; */
	margin-right: 5px;
}
.widget a {
	color: inherit;
}
/*--------------------------------------------------------------11.0 Site Content--------------------------------------------------------------*/
#primary,
#secondary {
	padding-top: 2em;
}
/*--------------------------------------------------------------11.1 Posts and pages--------------------------------------------------------------*/
.sticky {
}
.bypostauthor {
}
.hentry {
	margin: 0 0 3.5em;
}
.single-post .hentry {
	margin: 0 0 1.5em;
}
/* Thumbnail */
.entry-thumbnail {
	padding: 1em 0 0em 0;
}
.single-portfolio .entry-thumbnail {
	padding: 2em 0 1.5em 0;
}
/* Entry Title */
.entry-title {
	font-weight: 700;
	font-size: 20px;
	letter-spacing: 2px;
	line-height: 1.4;
	margin-top: 0;
	margin-bottom: 0;
	text-transform: uppercase;
}
.entry-title a {
	text-decoration: none;
	color: #222;
}
.entry-title a:hover {
	color: #ccc !important;
}
/* Entry Content */
.entry-content {
	white-space: normal;
}
.entry-content p:last-child {
	margin-bottom: 0;
}
a.more-link {
	position: relative;
	text-decoration: none;
	font-style: italic;
	text-transform: lowercase;
	margin-left: 3px;
}
a.more-links:before {
	content: "...";
	text-decoration: none;
	margin-right: 3px;
}
a.more-link {
	text-decoration: underline;
}
.entry-content ul li,
.entry-content ol li,
#comments ul li,
#comments ul li {
	margin-bottom: 12px;
}
.page-content,
.entry-content,
.entry-summary {
	margin: 1em 0;
}
.no-results .page-content {
	margin: 1.5em 0;
	padding-bottom: 1em;
}
/* Entry Meta */
.entry-meta {
	font-size: 13px;
	font-style: italic;
	font-weight: 300;
	letter-spacing: 1px;
}
.entry-meta a {
	color: #7f7f7f !important;
	text-decoration: none;
}
.entry-meta a:hover {
	text-decoration: underline;
}
.entry-meta .comments-link:before {
	content: " - ";
}
.byline,
.updated {
	display: none;
}
.single .byline,
.group-blog .byline {
	display: inline;
}
/* Page Links */
.page-links {
	clear: both;
	margin: 0 0 1.5em;
	word-spacing: 18px;
}
/* Entry Footer */
.entry-footer {
	font-style: italic;
	padding-top: 1em;
	padding-bottom: 0.5em;
}
.single-portfolio .entry-footer {
	padding: 0;
}
.entry-footer a {
	color: #7f7f7f !important;
	text-decoration: none;
}
.entry-footer a:hover {
	text-decoration: underline;
}
.blog .entry-footer,
.search-results .entry-footer,
.archive .entry-footer {
	padding-bottom: 2em;
}
.page .entry-footer {
	border-bottom: 0;
}
.entry-meta {
	margin-top: 5px;
}
/* Entry Navigation */
.meta-nav {
	font-size: 13px;
}
.nav-links a {
	text-decoration: none;
}
/* Archives Page */
.page-header {
	margin-bottom: 1.5em;
}
.page-title {
	line-height: 1;
	margin-top: 0;
	text-transform: capitalize;
}
.page-title:before {
	content: "#";
}
.page-header p {
	font-style: italic;
	margin-bottom: 0.5em;
}
/* 404 Page */
.error-404 .widget,
.error-404 .widget-title {
	text-align: left;
	border: 0;
}
/* Go to top */
.smoothup {
	bottom: 22px;
	display: none;
	position: fixed;
	right: 10px;
	transition: all 0.2s ease-in-out 0s;
}
a.smoothup {
	text-decoration: none;
}
/*--------------------------------------------------------------11.2 Asides--------------------------------------------------------------*/
.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
	display: none;
}
/*--------------------------------------------------------------11.3 Gallery--------------------------------------------------------------*/
.blog .format-gallery .entry-thumbnail,
.archive .format-gallery .entry-thumbnail,
.single-post .format-gallery .entry-thumbnail {
	display: none;
}
/*--------------------------------------------------------------11.4 Image--------------------------------------------------------------*/
.blog .format-image .entry-thumbnail,
.archive .format-image .entry-thumbnail,
.single-post .format-image .entry-thumbnail {
	display: none;
}
/*--------------------------------------------------------------11.5 Comments--------------------------------------------------------------*/
.comment-list {
	list-style: none;
	margin-left: 0;
}
.comment-body {
	border-bottom: 1px solid #f2f2f2;
	padding: 20px 0;
}
.comment-author {
	float: left;
}
.comment-author img {
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	border-radius: 50%;
	display: block;
}
.comment-meta {
	padding-bottom: 20px;
}
.comment-meta .fn {
	color: #24282d;
	font-weight: 900;
	font-style: normal;
	text-transform: uppercase;
}
.comment-meta .fn a {
	color: #404040;
	text-decoration: none;
}
.comment-meta-details {
	color: #7f8d8c;
	font-size: 0.9em;
	font-style: italic;
	line-height: 1.3;
	padding-top: 5px;
	text-align: center;
	text-align: left;
}
.comment-meta-details a {
	text-decoration: none;
}
.comment-content {
	margin-left: 80px;
}
.comment-content a {
	word-wrap: break-word;
}
.comment-list .children {
	list-style: none;
	margin-left: 80px;
	position: relative;
}
.comment-list .children .depth-5 {
	padding-top: 30px;
}
/*--------------------------------------------------------------12.0 Footer--------------------------------------------------------------*/
.site-footer {
	padding: 2em 0;
	text-align: center;
	background-color: #333;
	color: #999;
}
.site-footer a {
	text-decoration: none;
	color: #999;
}
/*--------------------------------------------------------------13.0 Infinite scroll--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .paging-navigation,/* Older / Newer Posts Navigation (always hidden) */
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}
/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before */
.infinity-end.neverending .site-footer {
	display: block;
}
/*--------------------------------------------------------------14.0 Media--------------------------------------------------------------*/
.page-content img.wp-smiley,
.entry-content img.wp-smiley,
.comment-content img.wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}
/* Make sure embeds and iframes fit their containers */
embed,
iframe,
object {
	max-width: 100%;
}
/*--------------------------------------------------------------14.1 Captions--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*="wp-image-"] {
	display: block;
	margin: 0 auto;
}
.wp-caption-text {
	font-style: italic;
	line-height: 24px;
	text-align: center;
}
.wp-caption .wp-caption-text {
	margin: 1em 0;
}
/*--------------------------------------------------------------14.2 Galleries--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}
.gallery-item {
	display: inline-block;
	margin-bottom: 10px;
	padding-right: 2.6%;
	text-align: center;
	vertical-align: top;
	width: 100%;
}
.gallery-item img {
	height: auto;
	width: 100%;
}
.gallery-columns-2 .gallery-item {
	max-width: 50%;
}
.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
	max-width: 25%;
}
.gallery-columns-5 .gallery-item {
	max-width: 20%;
}
.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
